@import url(https://fonts.googleapis.com/css2?family=Cookie&display=swap);
body {
    font-family: "MS Sans Serif", 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

    margin: 0;
    padding: 0;
    background-size: cover;
    /* background-repeat: no-repeat; */
    background-position: center;
    min-height: 100vh;
}

body::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background-image: url(/static/media/confetti-background.114c99c5.jpg);
    background-size: cover;
    background-position: center;
    filter: blur(5px);
    /* Adjust blur strength */
    width: 100vw;
    height: 100vh;
}


h1.main-title {
    color: #714aa5;
    text-align: center;
    font-family: "Cookie", sans-serif;
    font-size: 60px;
    margin-bottom: 0;
    margin-top: 0;
}

h2.event-title,
h2.rsvp-title {
    color: #714aa5;
    text-align: center;
    font-family: "Cookie", sans-serif;
    font-size: 36px;
    margin-top: 0;
}

h3 {
    color: #714aa5;
    text-align: center;
    font-family: "Cookie", sans-serif;
    font-size: 30px;
    margin-bottom: 0;
    margin-top: 0;
}

.main-bg {
    width: 100vw;
    min-height: 100vh;
    padding: 0;
}

.header-row {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 0px;
    gap: 0px;
    margin-top: 30px;
    margin-bottom: 10px;
}

.header-center {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 50px;
    gap: 50px
}

.header-bear {
    width: 100px;
    height: auto;
}

.content-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    grid-gap: 40px;
    gap: 40px;
    width: 90vw;
    max-width: 1200px;
    margin: 0 auto;
    padding-bottom: 40px;
}

.event-info {
    background: rgba(255, 255, 255, 0.7);
    border-radius: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    padding: 32px 32px 24px 32px;
    flex: 2 1;
    margin-top: 20px;
    font-size: 20px;
}

.event-info ul {
    margin-top: 8px;
    margin-bottom: 8px;
    padding-left: 24px;
}

.event-info p {
    margin: 10px 0;
}

.rsvp-section {
    background: rgba(255, 255, 255, 0.8);
    border-radius: 24px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.07);
    padding: 32px 24px 24px 24px;
    flex: 1.2 1;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rsvp-title {
    margin-bottom: 0;
}

.rsvp-deadline {
    text-align: center;
    font-size: 16px;
    margin-bottom: 18px;
    color: #2e1d44;
}

.rsvp-form {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    width: 100%;
    max-width: 350px;
    margin: 0 auto 18px auto;
}

.rsvp-form input,
.rsvp-form select,
.rsvp-form textarea {
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ccc;
    font-family: inherit;
}

.rsvp-form textarea::placeholder {
    font-family: inherit;
    color: #b48ecb;
    font-size: 15px;
    font-style: italic;
}

.rsvp-btn {
    background-color: #c18ddf;
    color: #333;
    border: none;
    cursor: pointer;
    font-size: 18px;
    border-radius: 6px;
    padding: 12px 0;
    margin-top: 8px;
    transition: background 0.2s;
}

.rsvp-btn:hover {
    background-color: #ae65d8;
}

.ultrasound-img {
    max-width: 320px;
    width: 100%;
    height: auto;
    display: block;
    margin: 18px auto 0 auto;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.10);
}

.footer {
    text-align: center;
    padding: 20px 0;
    background-color: #333;
    color: white;
}

.rsvp form {
    display: flex;
    flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
    margin: 0 auto;
    max-width: 400px;
}

.rsvp input,
.rsvp select,
.rsvp textarea,
.rsvp button {
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ccc;
}

.rsvp textarea::placeholder {
    font-family: "MS Sans Serif", 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.rsvp button {
    background-color: #c18ddf;
    color: #333;
    border: none;
    cursor: pointer;
}

.rsvp button:hover {
    background-color: #ae65d8;
    /* cursor: pointer; */
}






.registry {
    background: linear-gradient(135deg, #f8e8f9, #e6f4fa, #fdf3e7);
    border-radius: 12px;
    padding: 1.5rem;
    margin: 2rem 0;
    text-align: center;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
}

.registry h2 {
    font-family: "Cookie", sans-serif;
    font-size: 1.6rem;
    color: #7b4fa3;
    /* Matches teddy bear purple */
    margin-bottom: 0.5rem;
}

.registry p {
    font-family: "MS Sans Serif", 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 1rem;
    color: #555;
    margin-bottom: 1rem;
}

.registry-btn {
    background-color: #c18ddf;
    color: #333;
    border: none;
    cursor: pointer;
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ccc;
    text-decoration: none;
}

.registry-btn:hover {
    background-color: #ae65d8;
}

.rsvp-form .recaptcha-container {
    width: 100%;
    margin: 0.5rem 0;
    display: flex;
    justify-content: center;
}

.countdown {
    width: 90vw;
    max-width: 1200px;
    margin: 0 auto 10px auto;
    padding: 18px 0;
    text-align: center;
    font-size: 1.7rem;
    color: #714aa5;
    background: rgba(255, 255, 255, 0.7);
    border-radius: 18px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.07);
    animation: fadeIn 2s;
}

.countdown-text {
    font-family: "MS Sans Serif", 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 1.5rem;
    color: #555
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 900px) {
    .content-row {
        flex-direction: column;
        grid-gap: 0px;
        gap: 0px;
        /* width: 90vw;
        max-width: 90vw; */
    }

    .event-info,
    .rsvp-section {
        margin-top: 10px;
        margin-right: 10px;
    }

    .header-bear {
        margin-left: 30px;
        margin-right: 0px;
    }

    .header-center {
        grid-gap: 10px;
        gap: 10px;
        margin-right: 20px;
    }
}
